import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Button } from 'react-bootstrap';
import * as actions from '../store/game/actions';
import { createTask } from '../utils/game';
import { MdPlayCircleOutline } from 'react-icons/md';

function checkComplete(state) {
  return state['answerSimple'] !== null && state['answerParticiple'] !== null;
}

function makeState(verbDef) {
  return {
    ...createTask(verbDef),
    answerSimple: null,
    answerParticiple: null
  };
}

function Task({ current, finished, incoming, changeTask }) {
  const [taskState, setTaskState] = useState(makeState(current));

  useEffect(() => {
    setTaskState(makeState(current));
  }, [current]);

  const setAnswer = (variant, key) => {
    if (taskState['answer' + key]) return;
    setTaskState({
      ...taskState,
      ['answer' + key]: variant
    });
  };

  function checkVariant(variant, key) {
    const answerKey = 'answer' + key;
    const stateKey = key.toLowerCase();

    return taskState[answerKey] === variant
      ? taskState.def[stateKey] === taskState[answerKey]
        ? 'm22-correct'
        : 'm22-fail'
      : taskState[answerKey] !== null && taskState.def[stateKey] === variant
      ? 'm22-correct'
      : '';
  }

  let tabIndex = 1;

  return (
    <div className="m22-task d-flex flex-column flex-fill align-items-center">
      <div className="m22-base w-100">{taskState.base}</div>
      <div className="m22-tast-progress d-flex flex-row px-5 w-100 justify-content-end">
        {finished.length + '/' + (incoming.length + finished.length)}
      </div>

      <div className="m22-simple m22-answer-block p-4 px-5 d-flex flex-column w-100">
        <h6 className="text-right font-italic">simple</h6>
        {taskState.simple.map((verb, idx) => (
          <Button
            variant="secondary"
            onClick={e => setAnswer(verb, 'Simple')}
            className={checkVariant(verb, 'Simple') + ' mb-3 mb-sm-4'}
            key={verb + idx}
            tabIndex={tabIndex++}
          >
            {verb}
          </Button>
        ))}
      </div>
      <div className="m22-participle m22-answer-block mx-4 px-5  d-flex flex-column w-100">
        <h6 className="text-right font-italic">participle</h6>
        {taskState.participle.map((verb, idx) => (
          <Button
            variant="secondary"
            onClick={e => setAnswer(verb, 'Participle')}
            className={checkVariant(verb, 'Participle') + ' mb-3 mb-sm-4'}
            key={verb + idx}
            tabIndex={tabIndex++}
          >
            {verb}
          </Button>
        ))}
        <div className="d-flex justify-content-end">
          <MdPlayCircleOutline
            onClick={e => {
              changeTask();
            }}
            onKeyPress={e => {
              if (e.key === 'Enter') {
                changeTask();
              }
            }}
            className={(checkComplete(taskState) ? 'active' : 'disabled') + ' m22-icon'}
            tabIndex={tabIndex++}
          />
        </div>
      </div>
    </div>
  );
}

const mapStateToProps = state => {
  return {
    current: state.game.current,
    finished: state.game.finished,
    incoming: state.game.incoming,
    status: state.game.status
  };
};

const mapDispatchToProps = {
  changeTask: actions.changeTask
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Task);
