import {CHANGE_TASK} from './actions'
import { shuffle } from '../../utils/array';

const raw = require('../../data/verbs.json')
const verbs = shuffle(raw.data)

const initialState = {

  current: verbs[0],
  finished: [],
  incoming: verbs.slice(1, verbs.length),
  status: 'bla'
}



export default function gameReducer(state = initialState, action) {
  switch (action.type) {

    case CHANGE_TASK:
      return {
        finished: [state.current, ...state.finished],
        current: state.incoming.length > 0 ? state.incoming[0]: null,
        incoming: state.incoming.slice(1, state.incoming.length),
        status: state.incoming.length === 0 ? 'You win' : null
      }
    default:
      return state

  }
}