import React from 'react'

import {connect} from 'react-redux'

function Incoming({ incoming }) {
  return <div className="m22-incoming">
    <ul>
    {
        incoming.map(verb =>
          <li key={verb.base}>{verb.base}</li>
        )
      }
      </ul>
  </div>

 }


const mapStateToProps = (state) => {
  return {
    incoming: state.game.incoming
  }
}

export default connect(mapStateToProps)(Incoming)